#default {
    background-position: top center !important;
    background-size: cover !important;
    height: 100vh;
}
.mobius__header {display: none;}
.mobius__system__detect {
    width: 190px;
    overflow: hidden;
    height: 30px;
    cursor: pointer;
}
.mobius__system__detect:hover .mobius__name {
    display: none;
}
.mobius__system__detect:hover .mobius__header {
    display: block;
    font-weight: 900;
}

a.link {
