    color: #f8f9fa;
}
a.link:hover {
    display: flex;
    justify-content: center;
    color: #fff;
    text-decoration: underline;
}

/* ----- Responsiv CSS Style for Mobius Terminal ----- */

@media screen and (max-width:767px) {
    #default {
        display: flex;
