}

.react-datepicker {
    font-family: 'Dosis', system-ui !important;
    box-shadow: 0px -3px 47px 7px #0000008c;
    background-color: #29292e;
    color: #fff !important;
    border: 1px solid #29292e;
    border-radius: 0.3rem;
    display: inline-block;
    position: relative;
}
.react-datepicker__day:hover, 
.react-datepicker__month-text:hover, 
.react-datepicker__quarter-text:hover, 
.react-datepicker__year-text:hover {
    border-radius: 0.3rem;
    background-color: #000000 !important;
}
.react-datepicker-popper[data-placement^="bottom"] 
.react-datepicker__triangle, 
.react-datepicker-popper[data-placement^="bottom"] 
.react-datepicker__triangle::before {
    border-top: none;
    border-bottom-color:#1a1a1d !important;
}
.react-datepicker__day--selected, 
.react-datepicker__day--in-selecting-range, 
.react-datepicker__day--in-range, 
.react-datepicker__month-text--selected, 
.react-datepicker__month-text--in-selecting-range, 
.react-datepicker__month-text--in-range, 
.react-datepicker__quarter-text--selected, 
.react-datepicker__quarter-text--in-selecting-range, 
.react-datepicker__quarter-text--in-range, 
.react-datepicker__year-text--selected, 
.react-datepicker__year-text--in-selecting-range, 
.react-datepicker__year-text--in-range {
    background-color: #235fe2!important;
    color: #000000 !important;
    font-weight: bold;
}
.react-datepicker-popper[data-placement^="bottom"] 
.react-datepicker__triangle::before {
    top: -1px;
    border-bottom-color: #4e4f54 !important;
}
.react-datepicker__current-month, 
.react-datepicker-time__header, 
.react-datepicker-year-header {
    color: #fff !important;
}
.react-datepicker__day-name, 
.react-datepicker__day, 
.react-datepicker__time-name {
    color: #fff !important;
}
.react-datepicker__header {
    text-align: center;
    background-color: #1a1a1d !important;
    border-bottom: 1px solid #1a1a1d !important;
    border-top-left-radius: 0.3rem;
    padding-top: 8px;
    position: relative;
}
.react-datepicker-popper[data-placement^="top"] 
.react-datepicker__triangle::before, 
.react-datepicker__year-read-view--down-arrow::before, 
.react-datepicker__month-read-view--down-arrow::before, 
.react-datepicker__month-year-read-view--down-arrow::before {
    bottom: -1px;
    border-top-color: #161618 !important;
}
.react-datepicker-popper[data-placement^="top"] 
.react-datepicker__triangle, 
.react-datepicker__year-read-view--down-arrow, 
.react-datepicker__month-read-view--down-arrow, 
.react-datepicker__month-year-read-view--down-arrow, 
.react-datepicker-popper[data-placement^="top"] 
.react-datepicker__triangle::before, 
.react-datepicker__year-read-view--down-arrow::before, 
.react-datepicker__month-read-view--down-arrow::before, 
.react-datepicker__month-year-read-view--down-arrow::before {
