:root {
    --tagify-dd-color-primary: $primary;
    --tagify-dd-bg-color: white;
}

.tagify {
    --tags-border-color: #6e7075;
    --tags-hover-border-color: #6e7075;
    --tags-focus-border-color: #554ccf;
    --tag-bg: #29292e;
    --tag-hover: #554ccf;
    --tag-text-color: #ced1d8;
    --tag-text-color--edit: #ced1d8;
    --tag-pad: 0.3em 0.5em;
    --tag-inset-shadow-size: 1.1em;
    --tag-invalid-color: #fb9ad0;
    --tag-invalid-bg: rgba(251, 154, 208, 0.5);
    --tag-remove-bg: rgba(251, 154, 208, 0.3);
    --tag-remove-btn-color: #ced1d8;
    --tag-remove-btn-bg: none;
    --tag-remove-btn-bg--hover: #f973be;
    --input-color: #ced1d8;
    --tag--min-width: 1ch;
    --tag--max-width: auto;
    --tag-hide-transition: .3s;
    --placeholder-color: rgba(206, 209, 216, 0.4);
    --placeholder-color-focus: rgba(206, 209, 216, 0.25);
    --loader-size: .8em;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    border: 1px solid #6e7075;
    border: 1px solid var(--tags-border-color);
    padding: 0;
    line-height: 1.1;
    cursor: text;
    outline: none;
    position: relative;
    transition: .1s;
}

@keyframes tags--bump {
    30% {
        transform: scale(1.2);
    }
}

@keyframes rotateLoader {
    to {
        transform: rotate(1turn);
    }
}

.tagify:hover {
    border-color: #6e7075;
    border-color: var(--tags-hover-border-color);
}

.tagify.tagify--focus {
    transition: 0s;
    border-color: #554ccf;
    border-color: var(--tags-focus-border-color);
}

.tagify[readonly]:not(.tagify--mix) {
    cursor: default;
}

.tagify[readonly]:not(.tagify--mix)>.tagify__input {
    visibility: hidden;
    width: 0;
    margin: 5px 0;
}

.tagify[readonly]:not(.tagify--mix) .tagify__tag__removeBtn {
    display: none;
}

.tagify[readonly]:not(.tagify--mix) .tagify__tag>div {
    padding: 0.3em 0.5em;
    padding: var(--tag-pad);
}

.tagify[readonly]:not(.tagify--mix) .tagify__tag>div::before {
    background: linear-gradient(45deg, var(--tag-bg) 25%, 
                transparent 25%, transparent 50%, var(--tag-bg) 50%, 
                var(--tag-bg) 75%, transparent 75%, transparent) 0/5px 5px;
    box-shadow: none;
    filter: brightness(0.95);
}

.tagify--loading .tagify__input::before {
    content: none;
}

.tagify--loading .tagify__input::after {
    content: '';
    vertical-align: middle;
    opacity: 1;
    width: .7em;
    height: .7em;
    width: var(--loader-size);
    height: var(--loader-size);
    border: 3px solid;
    border-color: #EEE #BBB #888 transparent;
    border-radius: 50%;
    animation: rotateLoader .4s infinite linear;
    margin: -2px 0 -2px .5em;
}

.tagify--loading .tagify__input:empty::after {
    margin-left: 0;
}

.tagify+input,
.tagify+textarea {
    display: none !important;
}

.tagify__tag {
    display: inline-flex;
    align-items: center;
    margin: 5px 0 5px 5px;
    position: relative;
    z-index: 1;
    outline: none;
    cursor: default;
    transition: .13s ease-out;
}

.tagify__tag>div {
    vertical-align: top;
    box-sizing: border-box;
    max-width: 100%;
    padding: 0.3em 0.5em;
    padding: var(--tag-pad);
    color: #ced1d8;
    color: var(--tag-text-color);
    line-height: inherit;
    border-radius: 3px;
    user-select: none;
    transition: .13s ease-out;
}

.tagify__tag>div>* {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
    vertical-align: top;
    min-width: var(--tag--min-width);
    max-width: var(--tag--max-width);
    transition: .8s ease, .1s color;
}

.tagify__tag>div>*[contenteditable] {
    outline: none;
    user-select: text;
    cursor: text;
    margin: -2px;
    padding: 2px;
    max-width: 350px;
}

.tagify__tag>div::before {
    content: '';
    position: absolute;
    border-radius: inherit;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    pointer-events: none;
    transition: 120ms ease;
    animation: tags--bump .3s ease-out 1;
    box-shadow: 0 0 0 1.1em #29292e inset;
    box-shadow: 0 0 0 var(--tag-inset-shadow-size) var(--tag-bg) inset;
}

.tagify__tag:hover:not([readonly]) div::before {
    top: -2px;
    right: -2px;
    bottom: -2px;
    left: -2px;
    box-shadow: 0 0 0 1.1em #554ccf inset;
    box-shadow: 0 0 0 var(--tag-inset-shadow-size) var(--tag-hover) inset;
}

.tagify__tag--loading {
    pointer-events: none;
}

.tagify__tag--loading .tagify__tag__removeBtn {
    display: none;
}

.tagify__tag--loading::after {
    --loader-size: .4em;
    content: '';
    vertical-align: middle;
    opacity: 1;
    width: .7em;
    height: .7em;
    width: var(--loader-size);
    height: var(--loader-size);
    border: 3px solid;
    border-color: #EEE #BBB #888 transparent;
    border-radius: 50%;
    animation: rotateLoader .4s infinite linear;
    margin: 0 .5em 0 -.1em;
}

.tagify__tag--flash div::before {
    animation: none;
}

.tagify__tag--hide {
    width: 0 !important;
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
    margin-right: 0;
    opacity: 0;
    transform: scale(0);
    transition: .3s;
    transition: var(--tag-hide-transition);
    pointer-events: none;
}

.tagify__tag.tagify--noAnim>div::before {
    animation: none;
}

.tagify__tag.tagify--notAllowed:not(.tagify__tag--editable) div>span {
    opacity: .5;
}

.tagify__tag.tagify--notAllowed:not(.tagify__tag--editable) div::before {
    box-shadow: 0 0 0 1.1em rgba(251, 154, 208, 0.5) inset !important;
    box-shadow: 0 0 0 var(--tag-inset-shadow-size) var(--tag-invalid-bg) inset !important;
    transition: .2s;
}

.tagify__tag[readonly] .tagify__tag__removeBtn {
    display: none;
}

.tagify__tag[readonly]>div::before {
    background: linear-gradient(45deg, var(--tag-bg) 25%, transparent 25%, transparent 50%, var(--tag-bg) 50%, var(--tag-bg) 75%, transparent 75%, transparent) 0/5px 5px;
    box-shadow: none;
    filter: brightness(0.95);
}

.tagify__tag--editable>div {
    color: #ced1d8;
    color: var(--tag-text-color--edit);
}

.tagify__tag--editable>div::before {
    box-shadow: 0 0 0 2px #554ccf inset !important;
    box-shadow: 0 0 0 2px var(--tag-hover) inset !important;
}

.tagify__tag--editable.tagify--invalid>div::before {
    box-shadow: 0 0 0 2px #fb9ad0 inset !important;
    box-shadow: 0 0 0 2px var(--tag-invalid-color) inset !important;
}

.tagify__tag__removeBtn {
    order: 5;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    cursor: pointer;
    font: 14px/1 monospace;
    background: none;
    background: var(--tag-remove-btn-bg);
    color: #ced1d8;
    color: var(--tag-remove-btn-color);
    width: 14px;
    height: 14px;
    margin-right: 4.66667px;
    margin-left: -4.66667px;
    transition: .2s ease-out;
}

.tagify__tag__removeBtn::after {
    content: "\00D7";
}

.tagify__tag__removeBtn:hover {
