@-webkit-keyframes 'passing-through' {
    0% {
        opacity: 0;
        transform: translateY(40px);
    }
    30%,
    70% {
        opacity: 1;
        transform: translateY(0px);
    }
    100% {
        opacity: 0;
        transform: translateY(-40px);
    }
}

